// Font variables
$fontFamilyCustomRegular: 'nationalregular';
$iconFontFamily: 'emporium-icons';

// Base font sizes
$rootFontSize: 16px;
$fontSizeXXSS: 0.625rem; // 10px
$fontSizeXXXS: 0.75rem; // 12px
$fontSizeXXS: 0.812rem; // 13px
$fontSizeXS: 0.875rem; // 14px
$fontSizeS: 0.937rem; // 15px
$fontSizeSM:1.0625rem;// 17px
$fontSizeMM:1.125rem; //18px
$fontSizeM: 1.25rem; // 20px
$fontsizeMedium:1.375rem;//22px
$fontSizeXM: 1.5rem; // 24px
$fontSizeXXM:1.625rem; //26px
$fontSizeL: 1.75rem; // 28px //  
$fontSizeXL: 1.875rem; // 30px //


// font weight
$fontWeightLight: 300;
$fontWeightNormal: 400; 
$fontWeightSemiBold: 500;
$fontWeightBold: 700;
$fontWeightExtraBold: 900;

// Icons size
$iconSize: 16px;
$iconSizeLrg: 24px;
$iconSizeXLrg: 30px;

// line-height
$lineHeightS: 1.7;
$lineHeight: 1.5;
$lineHeightL: 1.2;
$lineHeightM: 1.3;
$lineHeightN:normal;

// Responsive
/* Extra small devices (phones, max-width 575px and down) */
$small_width: 575px;

/* Small devices (portrait tablets and large phones, min-width 600px and up) */
$mobile_width: 576px;

/* Medium devices (landscape tablets, min-width: 768px and up) */
$tablet_width: 768px;

/* Large devices (laptops/desktops, min-width: 992px and up) */
$landscapeTablet_width: 992px;

/*  (large laptops and desktops, min-width: 1200px and up) */
$desktop_width: 1200px;


/* Extra large devices (large laptops and desktops, min-width: 1200px and up) */
$wide_width: 1379px;

/* Extra large devices (min-width: 1400px and up) */
$extraWide_width: 1441px;

