﻿@import "../helpers/helpers";

.header {
    width: auto;

    &__mobileHeader {

        @include media(landscapeTablet) {
            display: none;
        }

        display: block;
    }

    .top-header {
        background-color: $primaryBrandClr;
        padding: 8px 25px 8px 25px;
        display: none;

        @include media(landscapeTablet) {
            display: block;
        }

        .top-content {
            a, p {
                color: $colorBaseWhite;
                margin-bottom: 0px;
            }
        }

        p {
            font-weight: 600;
        }

        .left, .right {
            font-size: 14px;
        }

        .left {
            text-align: left;
        }

        .right {
            text-align: right;
        }
    }

    .sticky {
        position: fixed;
        top: 0;
        width: 100%;
    }

    .sticky + .content {
        padding-top: 102px;
    }

    .header-upper {
        border: none;
        display: none;
        background-color: $colorBaseWhite;
        border-bottom: 1px solid $grayborder;
        height: 64px;

        @include media(landscapeTablet) {
            display: block;
        }



        .upper-header-wraper {
            display: flex;
            align-items: center;
            height: 64px;

            .header-menu {
                text-align: left;
                border: none;
                margin: 0px;
                padding: 0px;
                width: auto;

                .top-menu {
                    margin-bottom: 0px;
                    display: flex;

                    li {
                        border: none;
                        padding: 0px;

                        a {
                            font-weight: 600;
                            text-transform: capitalize;
                            color: $textcolor;
                            position: relative;
                            white-space: nowrap;
                            font-size: $fontSizeMM;

                            &:after {
                                bottom: -6px;
                                content: "";
                                display: block;
                                height: 2px;
                                left: 50%;
                                position: absolute;
                                background: $primaryBrandClr;
                                transition: width .3s ease 0s,left .3s ease 0s;
                                width: 0%;
                            }

                            &:hover {
                                text-decoration: none;

                                &:after {
                                    width: 100%;
                                    left: 0px;
                                }
                            }
                        }
                    }
                }
            }

            .store-search-box {
                display: inline-flex;
                margin-left: 20px;

                .search-box-button {
                    opacity: 0;

                    &:before {
                    }
                }

                .search-box-text {
                    border-radius: 4px;
                    border: 0px;
                    width: 425px;
                    background: url("../../Content/images/search-button.png") no-repeat;
                    background-color: $secbgcolor;
                    background-position: 10px;
                    padding-left: 30px;
                }
            }
        }




        .header-links-wrapper {
            white-space: pre;

            ul {
                li {
                    a {

                        &:hover {
                            color: $primaryBrandClr;
                        }
                    }
                }

                .top-sapertor {
                    font-weight: $fontWeightBold;
                    color: $bgcolorButtonGrey;
                    font-size: $fontSizeSM;
                }

                #topcartlink {
                    background-color: $colorBaseWhite;
                    padding: 10px;

                    a {
                        border: 1px solid $primaryBrandClr;
                        border-radius: 4px;
                        line-height: 30px;
                        position: relative;
                        background: none;
                        background-color: $primaryBrandClr;
                        padding: 0 0px 0px 20px;

                        &:before {
                            content: "";
                            width: 100%;
                            height: 20px;
                            position: absolute;
                            left: 5px;
                            top: 8px;
                            background: url("../images/cart-img.png")no-repeat;
                        }

                        span {
                            line-height: 35px;
                            color: $colorBaseWhite;
                            font-weight: 600;
                            text-transform: capitalize;
                            font-size: 16px;
                            padding: 0px 8px;
                        }

                        .cart-qty {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}



