@import "../helpers/_helpers";

.html-home-page {
    .master-wrapper-content {
        width: 100%;
    }

    .center-1 {
        margin-bottom: 0px;
    }

    .home-page {
        .page-body {
            .topic-block {
                margin-bottom: 0px;
            }

            .theme-custom {
                margin-top: 0px;
            }

            .address-box {
                left: 0;
                right: 0;
                bottom: -40px;
                border: 1px solid $secborder;
                display: flex;
                padding: 0 32px 32px;
                max-width: 726px;
                box-sizing: border-box;
                box-shadow: 0px 4px 24px rgb(0 0 0 / 10%);
                align-items: center;
                border-radius: 8px;
                flex-direction: column;
                justify-content: center;
                background-color: $boxcolor;
                margin: -16rem auto;
                position: relative;
                z-index: 99;

                .adreess-title {
                    margin-bottom: 20px;
                    margin-top: 20px;
                }

                .address-box-wrapper {
                    width: 100%;

                    .checkbox-wrapper {
                        .checkbox-text {
                            color: $textcolor;
                            margin-left: 10px;
                            position: relative;
                            height: auto;
                            display: inline-block;

                            &:after {
                                content: " ";
                                background: url("../images/gift.png") no-repeat bottom;
                                right: -80px;
                                top: 0px;
                                position: absolute;
                                height: 100%;
                                width: 100%;
                            }
                        }

                        input {
                            vertical-align: baseline;

                            &:before {
                                width: 22px;
                                height: 22px;
                                content: "";
                                display: inline-block;
                                border-width: 1px;
                                border-style: solid;
                                border-color: $colorBasetext;
                                background: $secbgcolor;
                            }
                        }

                        input[type="checkbox"]:checked {

                            &:before {
                                background: url("../images/chekbox.png") no-repeat;
                                border-color: $primaryBrandClr-hover;
                                background-color: $primaryBrandClr-hover;
                            }
                        }
                    }

                    .input-style {
                        height: auto;
                        padding-bottom: 2px;
                    }

                    .changelabel {

                        #fname {
                            font-size: $fontSizeXXXS;
                            top: 5px;
                            padding-bottom: 30px;
                        }
                    }

                    .input-box {
                        padding: 10px;
                        background-color: $colorBaseWhite;
                        border-radius: 4px;
                        margin-top: 10px;
                        position: relative;

                        .enter-delivery-address-search {
                            border: 2px solid $borderColor;
                            border-radius: 4px;
                            position: relative;
                            padding: 0 58px;
                            width: 72%;
                            display: inline-block;


                            label {
                                position: absolute;
                                top: 15px;
                                left: 60px;
                                opacity: 1;
                                transition: all .5s ease;
                                pointer-events: none;
                                z-index: 9;
                            }

                            &:before {
                                content: "";
                                width: 14%;
                                height: 25px;
                                position: absolute;
                                left: 14px;
                                top: 14px;
                                background: url('../images/location.png') no-repeat;
                                z-index: 2;
                            }

                            .textbox {
                                width: 100%;
                                height: 100%;
                                border: none;
                                border-radius: 0;
                                box-shadow: none;
                                height: 54px;
                                line-height: 54px;
                                position: relative;
                            }
                        }

                        .changelabel {
                            border: 2px solid $colorDarkGrey;

                            label {
                                font-size: $fontSizeXXXS;
                                top: 5px;
                                opacity: 1;
                            }
                        }

                        button {
                            height: 54px;
                            background-color: $primaryBrandClr;
                            color: $colorBaseWhite;
                            font-size: 20px;
                            border: none;
                            border-radius: 4px;
                            padding: 0px 20px;
                            margin-left: 7px;
                            vertical-align: middle;
                            width: 25%;
                            line-height: 20px;

                            &:hover {
                                background-color: $primaryBrandClr-hover;
                            }
                        }
                    }
                }

                .allow-btn {
                    display: none;
                    height: 45px;
                    background-color: $colorBaseWhite;
                    width: 100%;
                    text-align: left;
                    border: 1px solid $grayborder;
                    height: 5vw;
                    position: relative;
                    max-height: 65px;

                    .allow-address {
                        font-size: 26px;
                        color: $textcolor;
                        text-transform: capitalize;
                        font-weight: $fontWeightBold;
                        padding-left: 65px;

                        &:before {
                            content: "";
                            width: 10%;
                            height: 50px;
                            position: absolute;
                            left: 22px;
                            top: 17px;
                            background: url(../images/loction-arrow.PNG) no-repeat;
                        }
                    }
                }
            }

            .first-collage {
                .collage-wrapper {


                    .collage-module {

                        .collage-gift-box {
                            left: 25px;
                            background-color: $boxseccolor;
                        }
                    }
                }
            }

            .category-module {
                background-color: $bgcolor;
                padding: 4vw 140px;

                .category-wrapper {
                    .caegory-title {
                        padding-bottom: 16px;
                    }

                    ul {
                        li {
                            padding: 8px 0px;

                            a {
                                color: $textcolor;
                                font-weight: bold;
                                text-transform: capitalize;
                                font-size: $fontSizeMM;

                                &:hover {
                                    color: $primaryBrandClr;
                                }
                            }

                            &:last-child {
                                a {
                                    position: relative;
                                    color: $primaryBrandClr;

                                    &:after {
                                        content: "";
                                        height: 100%;
                                        width: 33%;
                                        position: absolute;
                                        background: url("../images/arrow-right.png") no-repeat bottom;
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .downlodable-wrapper {
                margin: 0 auto;
                width: auto;
                background: $secbgcolor;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-between;
                position: relative;
                overflow: hidden;
                height: 224px;

                .module-content {
                    background: $bgcolor;
                    margin: 0 auto;
                    width: 65%;
                    z-index: 10;
                    padding: 33px 56px;
                    background-color: $bgcolor;

                    h2 {
                        color: $textcolor;
                    }

                    .title {
                        font-size: $fontSizeXM;
                        text-align: left;
                        margin-bottom: 15px;
                    }

                    p {
                        color: $colorBlack;
                    }

                    .input-wrapper {
                        input {
                            width: 60%;
                            height: 49px;
                        }

                        button {
                            background-color: $primaryBrandClr;
                            padding: 24px 26px;
                            border-radius: 0 3px 3px 0;
                            border: 1px solid $primaryBrandClr;
                            border-left: none;
                            margin-right: 30px;
                            vertical-align: bottom;
                            margin-left: -4px;
                            position: relative;

                            &:hover {
                                background-color: $primaryBrandClr-hover;
                            }

                            &:after {
                                content: "";
                                top: -10px;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                position: absolute;
                                background: url("../images/arrow-right-white.png") no-repeat bottom;
                            }
                        }
                    }
                }

                .image-wrapper {
                    width: 35%;
                    z-index: 10;
                    position: relative;
                    pointer-events: none;
                    background: $imgbackground;
                    display: flex;
                    justify-content: center;
                }
            }

            .social-section {
                .social-details {
                    max-width: 88vw;
                    margin: 0 auto 4vw;
                    padding: 10px 0;

                    .social-wrapper {
                        box-shadow: 0 2px 4px 0 rgb(0 0 0 / 50%);
                        border-radius: $borderRadius;
                        height: 343px;
                    }

                    .social-info {
                        padding: 62px 16px;
                        text-align: center;
                        margin: 0 auto 32px;
                        max-width: 355px;


                        p {
                            font-size: $fontSizeXS;
                            line-height: 25px;
                        }

                        .user-name {
                            color: $colorBlack;
                            font-weight: $fontWeightBold;
                            font-size: $fontSizeS;
                        }

                        .icons {
                            position: relative;
                            width: 30px;
                            height: 30px;
                            margin: 0 auto;

                            &:after {
                                content: "";
                                top: 10px;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                position: absolute;
                                background: url("../images/social-twitter.png") no-repeat bottom;
                            }
                        }
                    }
                }
            }

            .market-sections {
                padding: 4vw 107px;
                margin: 0;
                background: $bgcolor;
                width: 100%;

                .market-place-list {
                    background: $bgcolor;

                    .list {
                        ul {
                            li {
                                margin-bottom: 1.8vw;

                                a {
                                    font-size: $fontSizeMM;
                                    font-weight: $fontWeightExtraBold;
                                    line-height: 40px;
                                    text-transform: capitalize;
                                    color: $textcolor;

                                    &:hover {
                                        color: $primaryBrandClr;
                                    }
                                }
                            }
                        }
                    }

                    .viwe-all-wrapper {
                        background: $colorBaseWhite;
                        width: 343px;
                        margin: 0 auto;
                        text-align: center;
                        padding: 13px 0;
                        border: 1px solid $darkborder;

                        a {
                            font-size: $fontSizeMM;
                            font-weight: $fontWeightExtraBold;
                            line-height: 25px;
                            color: $primaryBrandClr;

                            &:hover {
                                text-decoration: none;
                            }
                        }
                    }
                }
            }
        }
    }

    .ui-dialog {
        width: 450px;

        .ui-dialog-titlebar {
            background-color: $colorBaseWhite;
            border-bottom: none;
        }

        .ui-dialog-content {
            padding: 0px !important;

            .logo {
                text-align: center;
                margin: 20px;
            }

            .modal-body {
                text-align: center;
                position: relative;
                padding: 0px;

                .popup-info {
                    max-width: 300px;
                    margin: auto;


                    h1 {
                        color: $textcolor;
                    }

                    h3 {
                        font-size: $fontSizeXXS;
                        font-weight: $fontWeightLight;
                        position: relative;
                    }
                }

                .button-wrapper {
                    background: $secbgcolor;

                    .buttons {
                        text-align: center;
                        background: $secbgcolor;
                        padding: 30px;
                        margin-bottom: 0px;

                        .verificatop-btn {
                            width: 146px;
                            height: 46px;
                            border-radius: $borderRadius;
                            text-align: center;
                            font-size: 16px;
                            line-height: 23px;
                        }

                        .verification-btn-no {
                            background: $colorBaseWhite;
                            border: 2px solid $textcolor;
                            color: $textcolor;
                            margin-right: 8px;
                        }

                        .verification-btn-yes {
                            background: $primaryBrandClr;
                            border: none;
                            color: $colorBaseWhite;
                        }
                    }

                    #urge-age-text {
                        h4 {
                            font-size: $fontSizeS;
                            color: $textcolor;
                            font-weight: $fontWeightLight;
                            padding-bottom: 30px;
                        }
                    }
                }
            }
        }
    }
}



    

