@import "_base";

/*********************
03. HEADINGS & LINKS
*********************/
.h, h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    font-weight: $fontWeightBold;
    margin: 0;
}
h1 {
    font-size: $fontSizeXL;
    color: $colorDarkGrey;
}

/* OTHER FONT STYLES
----------------------------------------------------------*/

b, strong {
    font-weight: $fontWeightExtraBold;
}

i,
em {
    font-weight: $fontWeightNormal;
    font-style: italic;
}
  
   


