@import "../helpers/helpers";

/*********** CSS RESET **********/
figure.containerZoom {
    background-position: 50% 50%;
    position: relative;
    width: 100%;
    overflow: hidden;
    cursor: zoom-in;
    margin: 0
}

//overlay class
.overlayOffCanvas {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1060;
    background-color: rgba(0,0,0,0);
    transition: background .3s ease;
}
.show {
    background-color: rgba(0,0,0,.4);
    display: block;
}



@font-face {
    font-family: 'nationalregular','National';
    src: url('../../fonts/national-regular-webfont.woff2') format('woff2'), url('../../fonts/national-regular-webfont.woff') format('woff');
    src: url('../fonts/National-Bold.woff2') format('woff2'), url('../fonts/national-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

figure.containerZoom img {
    transition: opacity .5s;
    display: block;
    width: 100%
}

figure.containerZoom.active img {
    opacity: 0
}
.row {
    margin: 0px;
}
* {
    margin: 0;
    outline: none;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
}

*:before, *:after {
    box-sizing: border-box;
}

html {
    margin: 0 !important;
    -webkit-text-size-adjust: none;
}

ol, ul {
    list-style: none;
    padding:0px;
}

a img {
    border: none;
}

a:active {
    outline: none;
}


.container-fluid{
    width:100%;
    padding:0px;
}

.inputs {
    input[type="text"], input[type="email"], select {
        font-size: $fontSizeXS;
    }
}
button::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
    margin: 0;
    border: 0;
    padding: 0;
}

input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: $fontSizeXS;
}

input::-moz-placeholder {
    opacity: 1;
}

input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 1000px #fff;
}


/*********** GLOBAL STYLES **********/

// base font size change accroding to windows
body {
    font-family: $iconFontFamily;
    line-height: $lineHeight;
    font-weight: $fontWeightNormal;
    color: $colorBasetext;
    font-size: $rootFontSize;
   
}

h2, h3, p {
    color: $colorBasetext;
}



:root {
    font-family: $fontFamilyCustomRegular;
}

    table {
        width: 100%;
        border-collapse: collapse;
        border: 1px solid $colorBorder;
    }

    p {
        line-height: 20px;
    }

    a {
        color: inherit;
        cursor: pointer;
    }

    img {
        max-width: 100%;
    }


*:before, *:after {
    font-family: "$iconFontFamily";
    box-sizing: border-box;
}

* {
    margin: 0;
    outline: none;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
}

.btn-first {
    background-color:$primaryBrandClr;
    color: $colorBaseWhite;
    &:hover{
        background-color:$primaryBrandClr-hover;
    }
}

//common home page header section

.common-header-content {
    max-width: 693px;
    margin: 70px auto 56px;

    h2{
        color:$textcolor;
    }
    .title {
        margin: 15px 0px;
    }

    p {
        text-align: center;
        font-size: 18px;
        color: $colorBlack;
        line-height: 35px;
    }
}

//common about us content

.topic-block {
    .topic-block-body {
        .aboutus-block {
            width: 90%;
            margin: 380px auto 0;
            color: $textcolor;
            text-align: center;


                h2, p {
                    margin: 10px 0;
                    text-align:center;
                }

                .about-wrapper {
                    margin: 10px 0;
                }

                .info-wrapper {
                    margin-top: 30px;

                    .numbers {
                        background-color: $textcolor;
                        border-radius: 50%;
                        color: $colorBaseWhite;
                        height: 76px;
                        width: 76px;
                        line-height: 40px;
                        font-size: 40px;
                        text-align: center;
                        padding: 10px 21px;
                        margin: 0 auto;
                    }

                    .about-details {
                        margin: 30px 15px;

                        h3 {
                            margin: 15px 0;
                            font-size: 18px;
                        }
                    }
                }
            
        }
    }
}

//collage sections
.collage-section {
    .collage-wrapper {
        height: 597px;
        display: flex;
        overflow: hidden;
        margin-bottom: 76px;

        .collage-img {
            position: absolute;
            max-width: 924px;
            max-height: 584px;
            border-radius: 8px;
            right: 25px;
        }

        .collage-module {
            width: 100%;
            display: flex;
            position: relative;
            max-width: 1350px;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            background-color: $colorBaseWhite;
            margin: 0 auto;

            .collage-gift-box {
                padding: 30px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-evenly;
                border-radius: 8px;
                z-index: 1;
                position: absolute;
                max-width: 597px;
                border: 1px solid $borderColor;

                .box-info {
                    .details {
                        text-align: center;
                    }

                    .title {
                        margin: 25px 0px;
                    }
                }

                .button-box {
                    width: 60%;
                    height: 55px;
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: space-evenly;
                    background-color: $primaryBrandClr;
                    margin: 0 auto;
                    border-radius: 4px;

                    &:hover {
                        background-color: $primaryBrandClr-hover;
                    }

                    .btn {
                        display: inline-flex;
                        position: relative;
                        align-items: center;
                        border-radius: 4px;
                        justify-content: center;
                        width: 100%;
                        height: 54px;

                        .title {
                            color: $colorBaseWhite;
                            font-size: $fontSizeSM;
                            font-weight: 600;
                        }
                    }
                }
            }
        }
    }
}

.second-collage {
    .collage-wrapper {
        margin-bottom: 0px;

        .collage-module {
            .collage-img {
                left: 25px;
            }

            .collage-gift-box {
                right: 25px;
                background-color: $colorBaseWhite;
                border: 1px solid $borderColor;
                height: 323px;
            }
        }
    }
}

.third-collage {
    .collage-wrapper {
        margin-bottom: 0px;

        .collage-module {

            .collage-gift-box {
                left: 25px;
                background-color: $colorBaseWhite;
                height: 323px;
                justify-content: center;

                .box-info {
                    .details {
                        font-style: italic;
                        margin-bottom: 0px;
                    }

                    .title {
                        position: relative;
                        width: 41px;
                        height: 35px;
                        left: 40%;

                        &:after {
                            content: "";
                            height: 100%;
                            width: 100%;
                            position: absolute;
                            background: url("../images/quote.png") no-repeat bottom;
                        }
                    }
                }

                .gift-card-wrapper {
                    background-color: $colorBaseWhite;
                    height: unset;

                    .cosmopolitan-btn {
                        border: none;

                        .title {
                            color:$textcolor;
                        }
                    }
                }
            }
        }
    }
}

//shop loction-list
.topic-block {
    margin-bottom: 0px;

    .topic-block-body {
        .social-info-wrapper {
            background: $bgcolor;

            .market-place-wrapper {
                margin-bottom: 0px;
                padding-top: 60px;

                p {
                    margin-bottom: 0px;
                }
            }
        }
    }
}

