@import "../helpers/helpers";

.footer {
    background-color: $secbgcolor;
    color: $colorBasetext;

    .follow-us {
        float: left;

        @include media(tablet) {
            margin-top: 0px;
        }

        .social {
            .networks {
                border-bottom: none;
                padding-bottom: 0px;
                margin: 0 0 18px;

                li {
                    a {
                        display: block;
                        width: 38px;
                        height: 38px;
                        font-size: 0;
                    }
                }

                .facebook {
                    a {
                        background: url(../images/facebooks.png) no-repeat;
                    }
                }

                .twitter {
                    a {
                        background: url(../images/twitter-icons.png) no-repeat;
                    }
                }

                .instagram {
                    a {
                        background: url(../images/instagrams.png) no-repeat;
                    }
                }

                .pinterst {
                    a {
                        background: url(../images/pintersts.png) no-repeat;
                    }
                }
            }
        }

        .get-app {
            .title {
                text-transform: capitalize;
                margin-bottom: 10px;

                ul {
                    margin-top: 10px;

                    li {
                        display: inline-block;

                        .apple, .android {
                            height: 36px;
                            width: 36px;
                            display: block;
                            background: url('../images/apple_logo.png') no-repeat;
                            margin-right: 10px;
                        }

                        .android {
                            background: url('../images/android_logos.png') no-repeat;
                        }
                    }
                }
            }
        }

        .payment-options {
            .payment-icon {
                ul {
                    li {
                        display: inline-block;

                        a {
                            height: 36px;
                            width: 36px;
                            display: block;
                            margin-right: 10px;
                        }

                        .visa {
                            background: url(../images/visa.png) no-repeat;
                        }

                        .american-express {
                            background: url(../images/american_cards.png) no-repeat;
                        }

                        .master-card {
                            background: url(../images/master_card.png) no-repeat;
                        }

                        .discover {
                            background: url(../images/discovers.png) no-repeat;
                        }
                    }
                }
            }
        }
    }

    .footer-upper {
        padding-left: 0px;
        padding-right: 0px;
        border-bottom: none;

        .footer-block {
            @include media(small) {
                text-align: center;
            }

            @include media(tablet) {
                width: 100%;
            }

            .title {
                background: none;

                strong {
                    font-family: National-bold, Arial, sans-serif;
                    font-weight: 900;
                    line-height: 2.4vw;
                    margin-bottom: 12px;

                    @include media(small) {
                        font-size: 4.7vw;
                        color: $colorBasetext;
                    }

                    @include media(tablet) {
                        font-size: 1.7vw;
                        color: $colorBasetext;
                    }

                    @include media(desktop) {
                        font-size: 1.5vw;
                    }
                }
            }

            .list {

                @include media(small) {
                    display: block;
                    background-color: $secbgcolor;
                }

                @include media(tablet) {
                    display: block;
                    background-color: $secbgcolor;
                    padding: 10px 0px;
                }

                li {
                    a {
                        font-weight: $fontWeightLight;
                        font-size: 1.12vw;
                        line-height: 1.6vw;

                        @include media(small) {
                            font-size: 4vw;
                        }

                        &:hover {
                            text-decoration-color: $primaryBrandClr;
                            color: $colorBasetext;
                        }
                    }
                }
            }
        }
    }

    .footer-lower {
        .footer-info {
            float: none;
            text-align: center;
        }
    }
}
